import React from "react"
import { ParallaxBanner as Pb } from 'react-scroll-parallax';

const ParallaxBanner = ({ url, alt, heading, subheading, topMargin, amount }) => {


    return (
        <Pb
            className="banner"
            layers={[
                {
                    image: url,
                    amount: amount??0.2,
                    props: { alt: alt, style:{top:topMargin??"-25vh"}},


                },

            ]}
            style={{
                height: '600px',

            }}
        >
            {heading && <h1 style={{ zIndex: 1, color: "white" }}>{heading}</h1>}
            {subheading && <h2 style={{ zIndex: 1, color: "white", fontSize:"2.5rem" }}>{subheading}</h2>}
        </Pb>
    )
}
export default ParallaxBanner;